import React, { useState, useEffect } from 'react';
import { Box } from '@mui/material';
import {
	YMaps,
	Map,
	Placemark,
	ZoomControl,
	FullscreenControl,
	SearchControl,
} from '@pbe/react-yandex-maps';
import {
	useGetCapacityMutation,
	useGetAllCapacityQuery,
} from '../store/services/restApi';
import Loader from './Loader';
import { useSelector } from 'react-redux';

const host =
	process.env.NODE_ENV === 'production' ? 'zxchrnk.com' : 'localhost:8000';

const MapPage = () => {
	const [X, setX] = useState(0);
	const [Y, setY] = useState(0);
	const [capacity, setCapacity] = useState();
	const allCapacities = useSelector(state => state.allCaps);

	const { isLoading, isError } = useGetAllCapacityQuery({
		districts: 3,
		count: 3,
	});

	const [content, setContent] = useState('');

	const [getCapacity] = useGetCapacityMutation();

	const setCoords = (x, y) => {
		setX(x);
		setY(y);
	};

	const defaultState = {
		center: [59.9397392, 30.3140793],
		zoom: 11,
		controls: [],
	};

	const mapClickHandler = async e => {
		const [x, y] = e.get('coords');
		const {data} = await getCapacity({x, y});
		setCoords(data.build.data.latitude, data.build.data.longitude);
		setCapacity(
			'capacity: ' +
				Math.floor(data.capacity.coefficient * 100) +
				'% ' +
				data.build.data.short_address,
		);
		setContent(data.problems?.map(problem => problem.reason).join('\n'));
	};

	return (
		<Box
			id="map"
			display="flex"
			flexDirection="column"
			justifyContent="center"
			alignItems="center"
			sx={{ height: '100%', width: '100%' }}>
			{isLoading ? (
				<Loader />
			) : (
				<YMaps
					query={{
						lang: 'en_RU',
						apikey: 'b4e76738-74b1-4875-9ff9-782c376e12a0',
					}}>
					<Map
						defaultState={defaultState}
						height={'90%'}
						width={'90%'}
						onClick={mapClickHandler}>
						{allCapacities.map((capacity, index) => {
							return (
								<Placemark
									key={index}
									geometry={[
										+capacity.latitude,
										+capacity.longitude,
									]}
									properties={{
										iconCaption:
											capacity.building?.short_address ||
											'',
										// iconContent: content,
										balloonContent: capacity.reason,
									}}
								/>
							);
						})}
						<Placemark
							geometry={[X, Y]}
							properties={{
								iconCaption: capacity,
								balloonContent: content,
							}}
						/>
						<ZoomControl options={{ float: 'left' }} />
						<FullscreenControl options={{ float: 'right' }} />
						<SearchControl
							options={{ float: 'left' }}
							defaultOptions={{
								noPlacemark: true,
							}}
						/>
					</Map>
				</YMaps>
			)}
		</Box>
	);
};

export default MapPage;
