import React, { useState } from 'react';
import classes from '../styles/Login.module.css';
import { TextField, Box } from '@mui/material';
import { Paper, Button, Link } from '@mui/material';
import { useNavigate } from 'react-router';
import { useDispatch } from 'react-redux';
import TextFieldPassword from './TextFieldPassword';
import { setUser } from '../store/slices/userSlice';

const Login = () => {
	const [user, setLoginUser] = useState({
		email: '',
		password: '',
	});

	const setUserEmail = (email) =>
		setLoginUser(prev => ({ ...prev, email }));
	const setUserPassword = (password) =>
		setLoginUser(prev => ({ ...prev, password }));

	const [error, setError] = useState(false);
	const dispatch = useDispatch();
	const navigate = useNavigate();

	const loginHandler = () => {
		if (
			user.email?.trim() === 'zxchrnk' &&
			user.password?.trim() === 'password'
		) {
			dispatch(setUser({...user, login: user.email}));
			window.sessionStorage.setItem('user', JSON.stringify(user));
			navigate('/app');
		} else {
			setError(true);
		}
	};

	return (
		<Box className={classes.login}>
			<Paper className={classes.Paper} elevation={5}>
				<form>
					<h2>Login</h2>
					<TextField
						fullWidth
						value={user.email || ''}
						label={'Login'}
						required
						onChange={e => {
							setUserEmail(e.target.value);
							setError(false);
						}}
					/>
					<TextFieldPassword
						onChangeEventFunction={e => {
							setUserPassword(e.target.value);
							setError(false);
						}}
						required
						value={user.password || ''}
						error={error}
						helperText={error ? 'Incorrect login/password' : ''}
					/>
					<Box className={classes.forgotPassword}>
						<Link href="#">Forgot password?</Link>
					</Box>
					<Button
						fullWidth
						variant="contained"
						onClick={loginHandler}>
						Log in
					</Button>
				</form>
			</Paper>
		</Box>
	);
};

export default Login;
