import { createSlice } from '@reduxjs/toolkit';
import { restApi } from '../services/restApi';

const initialState = [];

export const capacitySlice = createSlice({
	name: 'capacity',
	initialState,
	reducers: {
		setAllCapacities: (
			state,
			action,
		) => {
			return action.payload;
		},

	},
	extraReducers: builder => {
		builder.addMatcher(restApi.endpoints.getAllCapacity.matchFulfilled, (state, action) => {
			return action.payload
		})
	}
});

export const { setAllCapacities } = capacitySlice.actions;
export default capacitySlice.reducer;
