import { createApi, fetchBaseQuery } from '@reduxjs/toolkit/query/react';

const host =
	process.env.NODE_ENV === 'production' ? 'zxchrnk.com' : 'localhost:8000';

export const restApi = createApi({
	reducerPath: 'applicationApi',
	baseQuery: fetchBaseQuery({
		baseUrl: `http://${host}/api`,
	}),
	endpoints: build => ({
		getCapacity: build.mutation({
			query: ({x, y}) => ({
				method: 'GET',
				url: `/get_capacity?x=${x}&y=${y}`,
			}),
		}),
		getAllCapacity: build.query({
			query: ({districts, count}) =>
				`/get_all_capacity?districts_count=${districts}&count_for_district=${count}`,
		}),
	}),
});

export const { useGetCapacityMutation, useGetAllCapacityQuery } = restApi;
